const comm = {
    state: {
        count: 0,
        // 
        carFloor: false,
        // 
        hasCarSelectSHow: false,
        hasCarSelectParternSHow: false,
        // ID
        addCarID: '',
        // 
        curArea: {}
    },
    mutations: {
        showFoot (state) {
            state.count = 0
        },
        hideFoot (state) {
            state.count = 1
        },
        showCarFloor (state) {
            state.carFloor = true
        },
        hidecarFloor (state) {
            state.carFloor = false
        },
        hideCarSelect (state) {
            state.hasCarSelectSHow = false
        },
        hideCarSelectPartern (state) {
            state.hasCarSelectParternSHow = false
        },
        changeCurCarNum (state) {
            // state.curCarNum
        }
    }
}

const user = {
    state: {
        userName: '',
        userToken: '',
        userInfo: {},
        // 
        userDefaultCar: {},
        // 
        userCarList: [],
        // 
        curCarNum: '',
        // ID
        curCarID: '',
        //  
        curCarData: {},
        //  
        curAreaID: '',
        curAreaName: ''
    },
    mutations: {
        logout (state) {
        },
        setUserInfo (state, userData) {
            state.userInfo = userData
        }
    }
}
// 
const cart = {
    state: {
        // 
        cartData: [],
        // 
        cartSelData: [],
        // 
        checkAllStatus: false,
        //   
        curAddress: {},
        // 
        cartConfirmSMData: {},
        cartConfirmMDData: [],
        cartConfirmDJ: 0,
        // 
        discountSeledList: [],
        // 
       selectCartData: [],
       // id
       selectArr: []
    },
    mutations: {
    }
}

// 
const posi = {
    state: {
        cartPosi: 0
    },
    mutations: {
    }
}

const data = {
    'comm': comm,
    'user': user,
    'cart': cart,
    'posi': posi
}

export default {
    data
}



